import { Injectable } from '@angular/core';

@Injectable({
  providedIn: 'root'
})
export class PopUpService {

  constructor() { }

  makeCapitalPopup(data: any): string {
    return String(`` +
      `<div>Capital: ${data.name}</div>` +
      `<div>State: ${data.state}</div>`);
      //`<div> Population: ${data.population}</div>`
  }

}
